<template>
  <nav class="navbar is-dark is-unselectable" role="navigation" aria-label="main navigation">
    <div class="container">
      <div class="navbar-brand">
        <span class="navbar-item">
          <p class="has-text-weight-bold is-size-4 has-text-primary is-absolute">Tic Tac Toe</p>
        </span>
        <div
          class="navbar-burger"
          data-target="navMenu"
          aria-label="menu"
          aria-expanded="false"
          @click="clickBurger"
          v-bind:class="{ 'is-active': getNavStatus.burgerOpen }"
        >
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
      <div class="navbar-menu" id="navMenu" :class="{ 'is-active': getNavStatus.burgerOpen }">
        <div class="navbar-end">
          <router-link class="navbar-item" :to="{ name: 'about' }">About</router-link>

          <router-link class="navbar-item" :to="{ name: 'game' }">Local Game</router-link>

          <router-link class="navbar-item" :to="{ name: 'rankings' }">Rankings</router-link>

          <span class="navbar-item">
            <router-link class="button is-primary" :to="{ name: 'register' }">
              <strong>Register</strong>
            </router-link>
          </span>
        </div>
      </div>
    </div>
  </nav>
</template>






<script>
export default {
  computed: {
    getNavStatus() {
      return this.navStatus;
    }
  },
  data() {
    return {
      showNav: false,
      navStatus: {
        burgerOpen: false,
        aboutActive: false,
        gameActive: false,
        rankingsActive: false,
        registerActive: false
      }
    };
  },
  methods: {
    clickBurger() {
      this.navStatus.burgerOpen = !this.navStatus.burgerOpen;
      console.log("hello");
    },
    clickAbout() {
      console.log("clicked about");
      this.navStatus.aboutActive = true;
      this.navStatus.burgerOpen = false;
      this.navStatus.gameActive = false;
      this.navStatus.rankingsActive = false;
      this.navStatus.registerActive = false;
      console.log(this.navStatus);
    },
    clickGame() {
      this.navStatus.burgerOpen = false;
      this.navStatus.aboutActive = false;
      this.navStatus.gameActive = true;
      this.navStatus.rankingsActive = false;
      this.navStatus.registerActive = false;
    },
    clickRankings() {
      this.navStatus.burgerOpen = false;
      this.navStatus.aboutActive = false;

      this.navStatus.gameActive = false;
      this.navStatus.rankingsActive = true;
      this.navStatus.registerActive = false;
    },
    clickRegister() {
      console.log("register clicked");
      this.navStatus.burgerOpen = false;
      this.navStatus.aboutActive = false;

      this.navStatus.gameActive = false;
      this.navStatus.rankingsActive = false;
      this.navStatus.registerActive = false;
    }
  }
};
</script>

<style scoped>
.navbar-menu.is-active {
  position: absolute;
  width: 100%;
}

.nav {
  background-color: transparent;
}
.button {
  transition: all 0.4s;
}
</style>
